body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
  margin: 0;
  padding: 0; }

html, body {
  height: 100%; }

body {
  background-color: #eee;
  color: #383838;
  font-family: "rooney-sans", sans-serif;
  font-size: 0.95em;
  font-weight: 300;
  line-height: 1.4; }

h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
ul, ol, dl, figure {
  margin-bottom: 10px; }

img {
  display: block;
  max-width: 100%;
  margin: 0 auto;
  padding: 10px 0;
  vertical-align: middle; }

ul, ol {
  margin-left: 20px; }

li > ul,
li > ol {
  margin-bottom: 0; }

h1, h2, h3, h4, h5, h6 {
  color: #126868;
  font-weight: 500; }

a {
  color: #126868;
  text-decoration: none; }
  a:hover {
    color: #072727;
    text-decoration: underline; }

blockquote {
  padding: 10px;
  border-left: 4px solid #c9c9c9;
  color: #5d5d60;
  font-size: 1.05em;
  font-style: italic;
  letter-spacing: -1px; }
  blockquote > :last-child {
    margin-bottom: 0; }

iframe {
  display: block;
  margin: 0 auto; }

pre, code {
  padding: 1px 5px;
  border: 1px solid #c9c9c9;
  border-radius: 3px;
  background-color: #eef;
  font-size: 1.1em; }

pre {
  padding: 8px 12px;
  overflow-x: scroll; }
  pre > code {
    padding: 0;
    border: 0; }

hr {
  margin-bottom: 20px;
  border: 0;
  border-bottom: 1px dashed #c9c9c9;
  background: #999; }

.l-wrapper {
  display: flex;
  min-height: 100%;
  flex-flow: column nowrap; }

.l-align-middle {
  max-width: calc(800px - (20px * 2));
  margin: 0 auto;
  padding: 0 20px; }
  @media screen and (max-width: 800px) {
    .l-align-middle {
      max-width: calc(800px - (20px));
      padding: 0 10px; } }

.l-section-spacing {
  margin-bottom: 20px; }

.l-page-content {
  padding: 20px;
  flex: 1; }

.l-footer-wrapper {
  display: flex;
  margin-left: -10px;
  color: #5d5d60;
  font-size: 1.2em;
  flex-flow: row nowrap; }

.l-footer-col {
  margin-bottom: 10px;
  padding-left: 10px; }

.l-footer-col-1 {
  width: calc(30% - 10px);
  margin-bottom: 10px;
  padding-left: 10px; }

.l-footer-col-2 {
  width: calc(70% - 20px);
  margin-bottom: 10px;
  padding-left: 10px;
  font-size: .8em; }

@media screen and (max-width: 800px) {
  .l-footer-col-1,
  .l-footer-col-2 {
    width: calc(100% - 10px); } }

@media screen and (max-width: 600px) {
  .l-footer-wrapper {
    display: flex;
    flex-flow: column nowrap; }
  .l-footer-col-1,
  .l-footer-col-2 {
    width: calc(100% - 10px); }
  .l-footer-col-2 {
    padding-top: 10px; } }

.site-header {
  height: 100px;
  background: url("../images/header-bg2.jpg") no-repeat center center; }

.site-title {
  margin-bottom: 0;
  color: #f2cb05;
  font-family: "museo-slab", serif;
  font-size: 3.8em;
  font-weight: 900;
  line-height: 75px;
  text-decoration: none;
  text-shadow: 4px 4px 2px #f22e2e; }
  @media screen and (max-width: 800px) {
    .site-title {
      padding-left: 10px;
      font-size: 2.2em; } }
  @media screen and (max-width: 600px) {
    .site-title {
      padding-left: 2px;
      font-size: 1.5em; } }

.site-title:hover, .site-title:visited {
  color: #f2cb05;
  text-decoration: none; }

.site-nav {
  margin-right: 0;
  margin-bottom: 0;
  padding-left: 70px;
  color: #fff;
  font-family: "museo-slab", serif;
  font-size: 1.1em;
  font-weight: 400; }
  @media screen and (max-width: 600px) {
    .site-nav {
      display: flex;
      position: absolute;
      top: 20px;
      right: 10px;
      margin: 0;
      padding-left: 0;
      border-radius: 8px;
      background-color: #fff;
      flex-flow: column nowrap;
      justify-content: flex-end; }
      .site-nav .menu-icon {
        display: block;
        position: relative;
        padding: 10px;
        line-height: 0;
        text-align: right; }
      .site-nav .trigger {
        display: none;
        text-align: right; }
      .site-nav:hover .trigger {
        display: block;
        padding-bottom: 5px; }
      .site-nav .page-link {
        display: block;
        padding: 5px 10px;
        color: #126868;
        text-shadow: none; } }

.menu-icon {
  display: none; }

.page-link {
  color: #fff;
  line-height: 1.4;
  text-shadow: 2px 2px 2px #f22e2e;
  vertical-align: bottom; }
  .page-link:not(:first-child) {
    margin-left: 20px; }

.site-footer {
  width: 100%;
  height: 100px;
  padding: 20px 0;
  background: url("../images/bottom-bg.png") no-repeat center center; }
  @media screen and (max-width: 600px) {
    .site-footer {
      height: 75px;
      padding: 10px; } }
  .site-footer h3 {
    font-family: "museo-slab", serif; }

.social {
  margin-left: 0;
  list-style: none; }
  .social li {
    padding-right: 10px;
    float: left;
    font-size: 1.8em; }

.section-heading {
  font-family: "museo-slab", serif;
  font-size: 1.2em;
  font-weight: 900; }

.post-list {
  margin-left: 0;
  list-style: none; }
  .post-list > li {
    margin-bottom: 10px; }

.post-meta {
  color: #5d5d60;
  font-size: 0.89em;
  font-style: italic; }

.post-title {
  color: #126868;
  font-family: "museo-slab", serif;
  font-size: 2.6em;
  letter-spacing: -1px;
  line-height: 1; }
  @media screen and (max-width: 800px) {
    .post-title {
      font-size: 2.4em; } }
  @media screen and (max-width: 600px) {
    .post-title {
      font-size: 1.8em; } }

.excerpt {
  margin-bottom: 20px; }

.post-content {
  margin-bottom: 20px;
  padding-bottom: 40px; }
  .post-content h1, .post-content h2, .post-content h3, .post-content h4, .post-content .byline {
    color: dark(#21bfbf, 20%);
    font-family: "museo-slab", serif; }
  .post-content h1 {
    margin-bottom: 0;
    font-size: 2em; }
    @media screen and (max-width: 800px) {
      .post-content h1 {
        font-size: 1.8em; } }
  .post-content h2 {
    padding-top: 20px;
    font-size: 1.8em; }
    @media screen and (max-width: 800px) {
      .post-content h2 {
        font-size: 1.6em; } }
  .post-content h3 {
    padding-top: 10px;
    font-size: 1.4em; }
    @media screen and (max-width: 800px) {
      .post-content h3 {
        font-size: 1.4em; } }
  .post-content h4 {
    padding-top: 10px;
    font-size: 1.2em; }
    @media screen and (max-width: 800px) {
      .post-content h4 {
        font-size: 1.2em; } }
  .post-content a, .post-content a:visited, .post-content a:hover {
    text-decoration: underline; }

.more {
  font-family: "museo-slab", serif;
  font-size: 1em;
  font-style: italic;
  font-weight: 700;
  text-align: right; }

.bottom-line {
  font-size: .85em;
  font-style: italic; }

.work {
  display: flex;
  padding-bottom: 40px;
  flex-flow: row wrap; }

.work-thumb {
  width: 200px;
  max-width: 200px;
  margin-top: 10px;
  margin-right: 20px;
  padding-right: 10px;
  font-weight: 700; }
  .work-thumb > img {
    padding: 0; }
  .work-thumb > a {
    text-decoration: none; }
  .work-thumb > a:hover {
    text-decoration: underline; }

.lightbox {
  margin-top: 20px; }
  .lightbox > ul {
    display: flex;
    list-style: none;
    flex-flow: row wrap; }
    .lightbox > ul > li {
      position: relative;
      margin-right: 20px;
      margin-bottom: 20px;
      background-color: #fff;
      color: #5d5d60;
      font-size: 0.89em;
      text-align: center;
      box-shadow: 0 8px 6px -6px rgba(48, 102, 102, 0.75);
      cursor: pointer; }
      .lightbox > ul > li > img {
        display: block;
        padding: 0; }
      .lightbox > ul > li :hover {
        box-shadow: 0 8px 6px -6px rgba(48, 102, 102, 0.75); }
        .lightbox > ul > li :hover .image-title {
          opacity: 1; }
        .lightbox > ul > li :hover .title {
          transform: scale(1); }

.image-title {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 145px;
  transition: all .5s;
  background: rgba(48, 102, 102, 0.75);
  opacity: 0;
  flex-flow: column nowrap;
  align-items: middle;
  justify-content: center; }

.title {
  transform: scale(0);
  transition: all .25s;
  color: #fff;
  font-family: "museo-slab", serif;
  font-size: 1.4em; }

.lb-title {
  display: block;
  padding: 5px 0;
  font-weight: 700;
  text-align: center; }

.lb-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.85); }

.lb-canvas {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 50px;
  height: 50px;
  padding: 10px;
  background: #fff;
  box-shadow: 0 0 20px 5px #000;
  flex-flow: column no-wrap;
  align-items: center; }

.lb-canvas-loading {
  background: #fff url("../images/spinner.gif") center center no-repeat; }

.lb-controls {
  position: fixed;
  right: 0;
  bottom: 30px;
  left: 0;
  width: 400px;
  margin: 0 auto;
  background: rgba(0, 0, 0, 0.75);
  color: #fff; }
  .lb-controls div {
    height: 30px;
    line-height: 30px; }
  .lb-controls .inactive {
    cursor: default;
    opacity: .1; }

.lb-prev, .lb-next {
  position: absolute;
  top: 0;
  padding: 5px 12px;
  background: #000;
  cursor: pointer; }

.lb-prev {
  left: 0;
  border-right: 1px solid rgba(255, 255, 255, 0.1); }

.lb-next {
  right: 0;
  border-left: 1px solid rgba(255, 255, 255, 0.1); }

.project-meta {
  margin-bottom: 20px;
  font-size: 1.1em; }
  .project-meta > dt {
    width: 75px;
    float: left;
    color: #126868;
    font-family: "museo-slab", serif; }
  .project-meta > dd {
    color: #5d5d60; }

.side-img {
  margin-right: 20px;
  float: left; }
  @media screen and (max-width: 600px) {
    .side-img {
      margin-right: 0 auto;
      float: none; } }

.return-to {
  padding-top: 40px; }
